  box-sizing: border-box;  
  padding: 0;
  margin: 0;
}

a{
  display: inline-block;
  min-width: 44px;
  min-height: 44px;
}


.skip-link {
  display: flex;
  position: absolute;
  margin: 20px 20px 0;
  top: -80px;
  left: 0;
  background-color: #22A39F;
  color: white;
  padding: 8px;
  z-index: 100;
  text-decoration: none;
  align-items: center;
}

.skip-link:focus {
  top: 0;
}


body {
  font-family: 'Quattrocento', serif;
  background-color: #1C1B1B;
  color: white;
  display: flex;
  flex-direction: column;
}

nav {
  width: 100%;
  padding: 20px;
  display: flex;
  justify-content: space-between;
}

nav .logo{
  font-size: 20px;
  padding-left: 26px;  
}

nav .navbar-list{
  padding: 10px;
  margin: 3px;

}

.navbar-list ul{
  display: flex;
  gap: 20px;
}

.navbar-list ul li{
  min-height: 46px;
  font-size: 20px;
  list-style-type: none;
  display: flex;
  justify-content: center;
  gap: 50px;
}


.navbar-list a{
  text-decoration: none;
  color: white;
  border-bottom: transparent;
  transition: all .18s ease-in;
}

.navbar-list a:hover{
  color: #22A39F;
  padding-bottom: 2px;
  border-bottom: 1px solid #22A39F;
}

.navbar-list a:focus{
  color: #22A39F;
  padding-bottom: 2px;
  border-bottom: 1px solid #22A39F;
}


.hero{
    background: url(${___CSS_LOADER_URL_REPLACEMENT_0___});
    display: flex;
    align-items: center;
    width: 100%;
    justify-content: center;
    background-size: cover;
    border-radius: 20px;
    color: #fff;
}

.first-menu{
  margin: 100px;
  height: 300px;
  max-width: 800px;
  display: flex;
  padding: 3px;

  align-items: center;

  border-radius: 50px;
  background: rgba(52, 52, 52, 0.93);
}

.restaurant-card a {
  color: white;
  text-decoration: none;
}

.description-1{
  padding: 20px;
}

.description-1 p{
  padding: 4px;
  line-height: 18px;
  font-size: 16px;
}

.image-1{
  padding-right: 15px;
}

.image-1 img{
  margin: auto;
  width: 250px;
  border-radius: 20px;
}

.first-menu button {
  
  margin: 5px;
  padding: 15px 45px;
  margin-block-start: 1rem;
  text-transform: uppercase;
  font-family: 'Quattrocento', serif;
  font-size: 15px;
  color: #FFFF;

  border: 1px solid ;
  background-color: transparent;
  cursor: pointer;
  transition: all .18s ease-in;
}

.first-menu button:hover{
  color: white;
  background-color: #22A39F;
}

.first-menu button:focus{
  color: white;
  background-color: #22A39F;
}

#about-us{

  display: flex;
  justify-content: space-around;
  color: #fff;
  padding: 100px 0;
}



.biografi{
  width: 800px;
  height: 500px;
  font-size: 18px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  gap: 10px;
}

.sosmed{
  margin: 10px;
  padding: 10px;
}

.sosmed ul {
  font-size: 25px;
  display: flex;
  gap: 20px ;
  list-style-type: none;
}

.sosmed ul li i{
  padding-right: 5px;
  align-items: center;
  font-size: 46px;
  color: white;
  width: 45px;
  height: 45px;
}

.sosmed a{
  width: 100px;
}

.photo-biografi{
  padding-right: 15px;
}

.photo-biografi img{
  margin: auto;
  width: 500px;
  border-radius: 50px;

}

#maincontent h1 {
  font-size: 35px;
  text-align: center;
}

.list-restaurant h1 {
  text-align: center;
  margin-top: 30px;
}

#daftar-restaurant {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-template-rows: repeat(2, 1fr);
  grid-column-gap: 0px;
  grid-row-gap: 0px;
  gap: 30px;
  margin: 30px;
  padding: 20px;
  background: rgba(52, 52, 52, 0.93)
  
}

  .restaurant-card {
    max-width: 100%;
    padding: 17px;
    background-color: black;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    border-radius: 8px;
    transition: transform 0.3s ease-in-out;
  }

  .restaurant-card p{
    overflow: hidden;
    text-overflow: ellipsis;
    display: -webkit-box;
    -webkit-line-clamp: 4; /* number of lines to show */
    -webkit-box-orient: vertical;
  }


  .restaurant-card h3 {
    padding: 6px;
  }

  .restaurant-image {
    width: 100%;
    height: 150px;
    object-fit: cover;
    border-radius: 8px;
  }
  
  .rating{
    display: flex;
    width: 100px;
    align-items: center;
    background-color: white;
    font-size: 12px;
    margin-top: 10px;
    margin-bottom: 10px;
    padding: 10px;
    border-top-right-radius: 10px;
    border-bottom-right-radius: 10px;
    color: black;
  }

  /* Detail */
.title-detail {
  text-align: center;
  margin-top: 30px;
}  
.detail_restaurant{
    display: flex;
    flex-wrap: wrap;
    margin: 20px;
    padding: 30px;
    background: rgba(52, 52, 52, 0.93)
}

.detail_restaurant h4 {
  font-style: italic;
  padding-top: 10px;
  font-size: 20px;
}

.restaurant_image{
    border-radius: 30px;
    width: 250px;
    height: 210px;

}
.restaurant_info{
    display: block;
    padding: 10px 26px;
}
.restaurant_info p{
    font-size: 18;
    padding: 4px;
}

.container-footer{
  width: 100%;
  padding: 30px;
  background-color: black;
}

.deskripsi-footer{
  display: flex;
  justify-content: space-between;
  align-items: center;
}

/* Like */

.like {
  width: 55px;
  height: 55px;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: grey;

  
  position: fixed;
  bottom: 16px;
  right: 16px;
  border-radius: 50%;
  border: 0;
  
  font-size: 18px;
  color: white;
  cursor: pointer;
}

/* favorite restaurant */

.list__restaurant h1{
  text-align: center;
  margin-top: 20px;
}

.favorite-restaurants {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  gap: 10px;
  margin: 30px;
  padding: 15px;
  background: rgba(52, 52, 52, 0.93)

}



/* footer */

.menu-footer{
  padding: 10px;
}

.menu-footer ul{
  list-style-type: none;
  display: flex;
  gap: 20px;
  font-size: 30px;
}

.menu-footer ul li i{
  color: white;
  width: 45px;
  height: 45px;
}

